.backdrop_footer {
  background: rgba(32, 13, 29, 0.6);
  backdrop-filter: blur(3px);
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 99;
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.backdrop_footer.is-hidden1 {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}
.modal_footer {
  width: 272px;
  height: 715px;
  background-color: #fff2fd;
  border-radius: 70px 0;
  padding-top: 50px;
  padding-right: 10px;
  padding-left: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  @media screen and(min-width: 480px) {
    width: 315px;
    height: 715px;
  }
  @media screen and(min-width: 768px) {
    width: 595px;
    height: 670px;
    border-radius: 200px 0px;
    padding-top: 70px;
    padding-left: 40px;
    padding-right: 40px;
  }
  @media screen and(min-width: 1280px) {
    border-radius: 200px 0px;
    padding-right: 100px;
    padding-left: 100px;
    padding-top: 70px;
    width: 850px;
    height: 700px;
  }
}

.modal-footer_title {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: normal;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #dc56c5;
  @media screen and(min-width: 768px) {
    font-size: 30px;
    line-height: 20px;
    margin-bottom: 25px;
  }
  @media screen and(min-width: 1280px) {
    font-size: 40px;
  }
}

.footer-modal_img {
  background-color: white;
  width: 100px;
  height: 150px;
  margin: 0;
  object-fit: cover;
  border-radius: 50px 0;
  @media screen and(min-width: 768px) {
    width: 150px;
    height: 200px;
  }
  @media screen and(min-width: 1280px) {
    width: 200px;
    height: 210px;
  }
}

.footer-modal_list {
  display: flex;
  flex-wrap: wrap;
}

.footer-modal_item {
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 10px;
  @media screen and(min-width: 768px) {
    margin-bottom: 15px;
  }
  @media screen and(min-width: 1280px) {
    margin-bottom: 20px;
    width: 200px;
  }
}

.footer-modal_text {
  text-align: center;
  width: 100px;
  font-size: 16px;
  line-height: 20px;
  color: #0e0e0e;
  @media screen and(min-width: 768px) {
    width: 150px;
    margin-top: 5px;
  }
  @media screen and(min-width: 1280px) {
    width: 200px;
    font-size: 20px;
  }
}

.modal-footer_close {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 25px;
  right: 25px;
  background: transparent;
  border: 0px;
  @media screen and(min-width: 1280px) {
    top: 30px;
    right: 30px;
  }
}

.modal-footer_close-svg {
  width: 17px;
  height: 17px;
  @media screen and(min-width: 1280px) {
    width: 20px;
    height: 20px;
  }
}
